function startRecord() {
  recognition.start();
  console.log("Ready to receive a command.");
  micBtn.disabled = true;
  document.querySelector('.loader-container').style.visibility="visible";
}
function stopRecord() {
  recognition.stop();
  console.log("Cancel receive a command.");
  micBtn.disabled = false;
}
